<template>
  <div id="Index">
    <Row style="margin-top: 30px;">
      <div class="container-jobs-list" v-for="(job, index) in showJobsList" :key="index">
        <Col span="5" offset="2" style="padding-bottom: 50px;">
        <Card style="width:400px; height: 230px; padding: 10px;">
          <p slot="title" style="font-weight: bold;">
            <Icon type="ios-arrow-forward"/>
            {{ job.JobName }}
          </p>
          <a slot="extra" @click="GotoJobDetails(job.id)">
            <Icon type="ios-loop-strong"></Icon>
            查看
          </a>
          <ul>
            <li>城市：{{ job.JobCity }}</li>
            <li>位置：{{ job.JobLoc }}</li>
            <li>薪酬：{{ job.JobSalary }}元</li>
            <li>工作时间：{{ job.JobWorkTime }}</li>
          </ul>
        </Card>
        </Col>
      </div>
    </Row>

    <Row>
      <Col>
      <Page :total="jobsCount" :page-size="pageSize" @on-change="changePage"/>
      </Col>
    </Row>
  </div>
</template>

<script>
  export default {
    name: 'Index',

    data() {
      return {
        jobsList: [],
        jobsCount: 0,
        pageSize: 9,
        showJobsList: [],
      }
    },

    created() {
      this.get_jobs_list(),
      this.$Notice.info({
        title: '测试账号',
        desc: '兼职用户：ptuser101，密码 123456； 公司用户：com01，密码 com01',
        duration: 12
      });
    },

    methods: {
      get_jobs_list() {
        this.$axios.get(this.GLOBAL.host + '/pt/jobs/').then(res => {
          this.jobsList = res.data
          this.jobsCount = this.jobsList.length
          if (this.jobsCount < this.pageSize) {
            this.showJobsList = this.jobsList
          } else {
            this.showJobsList = this.jobsList.slice(0, this.pageSize)
          }
        }).catch(err => {
          console.log(err);
          this.$Message.error('获取工作列表失败，请稍后重试')
        })
      },

      GotoJobDetails(jobID) {
        this.$router.push({name: 'JobDetails', params: {id: jobID}})
      },

      changePage(index) {
        var _start = (index - 1) * this.pageSize
        var _end = index * this.pageSize
        this.showJobsList = this.jobsList.slice(_start, _end)
      },
    }
  }
</script>

<style>
  .container-jobs-list ul {
    list-style: none;
    text-align: left;
  }

  .container-jobs-list li {
    padding: 5px 0;
  }
</style>



