<template>
  <div id="app">
    <Layout>
      <Header style="background: rgba(179, 214, 209, 0.41);">
        <router-link :to="'/vue'">
          <Icon type="logo-vimeo" size="20" style="padding-right: 10px;"/>
          <span>关于</span>
        </router-link>
        <Divider type="vertical"/>
        <router-link :to="'/'">
          <Icon type="md-analytics" size="20" style="padding-right: 10px;"/>
          <span>首页</span>
        </router-link>
        <Divider type="vertical"/>
        <router-link :to="'/login'">
          <Icon type="md-contact" size="20" style="padding-right: 10px;"/>
          <span>登录/注册</span>
        </router-link>
        <span v-if="username_logged" style="margin-left: 25px; padding: 12px; font-size: 18px; border-radius: 10px; background: #F5F7F9;">你好，{{ username_logged }} 欢迎登录<Divider type="vertical"/><span @click="GotoPersonalCenter" style="color: #57A3F3; cursor: pointer;">个人中心</span><Divider type="vertical"/><span @click="logout" style="color: #57A3F3; cursor: pointer;">注销</span></span>
      </Header>
      <Content>
        <router-view></router-view>
      </Content>
      <Footer>-- 兼职平台 --</Footer>
    </Layout>
  </div>
</template>

<script>
  export default {
    name: 'app',

    data() {
      return{
        username_logged: this.$cookies.get('username_logged'),
      }
    },

    methods: {
      GotoPersonalCenter() {
        this.$router.replace({name: 'PersonalCenter'})
      },

      logout() {
        this.$axios.get(this.GLOBAL.host + '/pt/logout/').then(res => {
          this.$cookies.remove('username_logged')
          this.$Message.success('注销成功')
          this.username_logged = ''
        }).catch(err => {
          this.$Message.error('注销失败，请稍后重试')
        })
      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  router-link {
    color: #DDD;
    font-size: 18px;
    padding-right: 20px;
  }

  router-link span {
    color: #DDD;
  }
</style>



