// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';

//import router from './router';
import VueRouter from 'vue-router';

import ViewUI from 'view-design';
import 'view-design/dist/styles/iview.css';

import axios from 'axios';
import VueCookies from 'vue-cookies'
import qs from 'qs';
import Global from '../static/global'

import HelloWorld from './components/HelloWorld';
import Index from './components/Index'
import Login from './components/Login';
import JobDetails from './components/JobDetails'
import CompanyDetails from './components/CompanyDetails'
import PersonalCenter from './components/PersonalCenter'

Vue.config.productionTip = false;

Vue.use(ViewUI);
Vue.use(VueRouter);

Vue.prototype.$axios = axios
Vue.use(VueCookies)
//axios.defaults.crossDomain = true
axios.defaults.withCredentials = true
Vue.prototype.$qs = qs
Vue.prototype.GLOBAL = Global

const routes =  [
  {path: '/vue', component: HelloWorld, name: "HelloWorld", meta: {title: "兼职平台 - 欢迎页"}},
  {path: '/', component: Index, name: "Index", meta: {title: "兼职平台 - 首页"}},
  {path: '/login', component: Login, name: "Login", meta: {title: "兼职平台 - 登录/注册"}},
  {path: '/jobDetails', component: JobDetails, name: "JobDetails", meta: {title: "兼职平台 - 兼职详情页"}},
  {path: '/companyDetails', component: CompanyDetails, name: "CompanyDetails", meta: {title: "兼职平台 - 公司页"}},
  {path: '/personalCenter', component: PersonalCenter, name: "PersonalCenter", meta: {title: "个人中心"}}
];


const router = new VueRouter({
  routes,
  // mode: 'history'
});

router.beforeEach((to, from, next) => {

    if (to.meta.title) {
      document.title = to.meta.title
    }

  if(to.path === '/jobDetails') {  // 是否有向 /jobDetails 传参
    if(!to.params.id) {
      alert('无传参')
      return
    } else {
      next()
    }

  }

  if(to.path === '/companyDetails') {
    if(!to.params.id) {
      alert('无传参')
      return
    } else {
      next()
    }

  }

  next()
})

new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router,
}).$mount('#app');



