<template>
  <div class="container-main" id="Login">


    <div class="switch-btn" style="margin: 35px auto;">
      <Row>
        <Col span="4" offset="10">
        <Button :type="showLoginTab?'primary':'default'" :size="buttonSize" icon="md-contact" @click="switchTab()">登录
        </Button>
        <Divider type="vertical"/>
        <Button :type="showRegTab?'primary':'default'" :size="buttonSize" icon="md-log-in" @click="switchTab()">注册
        </Button>
        </Col>
      </Row>
    </div>

    <div class="container-login" v-show="showLoginTab">
      <Row>
        <Col span="4" offset="10">
        <Form ref="formLogin" :model="formLogin" :rules="ruleLogin">
          <FormItem prop="username">
            <Input type="text" v-model="formLogin.username" placeholder="用户名">
            <Icon type="ios-person-outline" slot="prepend"></Icon>
            </Input>
          </FormItem>

          <FormItem prop="password">
            <Input type="password" v-model="formLogin.password" placeholder="密码">
            <Icon type="ios-lock-outline" slot="prepend"></Icon>
            </Input>
          </FormItem>
          <FormItem>
            <Button type="default" @click="login">登录</Button>
          </FormItem>
        </Form>
        </Col>
      </Row>
    </div>

    <div class="container-reg" v-show="showRegTab">
      <Row>
        <Col span="4" offset="10">
        <RadioGroup v-model="regType" type="button" style="margin-bottom: 20px;">
          <span @click="switchRegType"><Radio label="兼职用户"></Radio></span>
          <span @click="switchRegType"><Radio label="公司用户"></Radio></span>
        </RadioGroup>
        </Col>
        <Col span="4" offset="10" v-show="isPTUser">
        <Form ref="formReg" :model="formReg" label-position="left" :rules="ruleReg" :label-width="80">
          <FormItem prop="username" label="用户名">
            <Input type="text" v-model="formReg.username"></Input>
          </FormItem>
          <FormItem prop="password" label="密码">
            <Input type="password" v-model="formReg.password"></Input>
          </FormItem>
          <FormItem prop="avatar" label="头像">
            <Upload ref="upload"
                    :format="['jpg','jpeg','png']"
                    :max-size="2048"
                    :show-upload-list="false"
                    :on-success="avatarUploadSuccess"
                    :on-format-error="avatarFormatError"
                    :on-exceeded-size="avatarMaxSize"
                    :on-error="avatarUploadError"
                    action="//120.79.85.160:80/pt/avatar/" v-model="formReg.avatar">
              <Button icon="ios-share-outline">上传头像 (可选)</Button>
            </Upload>
          </FormItem>
          <FormItem prop="city" label="所在城市">
            <Input type="text" v-model="formReg.city"></Input>
          </FormItem>
          <FormItem prop="phone_number" label="手机号">
            <Input type="text" v-model="formReg.phone_number"></Input>
          </FormItem>
          <FormItem label="个人简介" prop="intro">
            <Input type="textarea" v-model="formReg.intro" :autosize="{minRows: 5, maxRows: 10}"
                   placeholder="可选，请输入一段个人简介"></Input>
          </FormItem>
          <FormItem>
            <Button type="default" @click="register">注册</Button>
          </FormItem>
        </Form>
        </Col>

        <Col span="4" offset="10" v-show="!isPTUser">
        <Form ref="formReg" :model="formReg" label-position="left" :rules="ruleReg" :label-width="80">
          <FormItem prop="companyUsername" label="公司名">
            <Input type="text" v-model="formReg.companyUsername"></Input>
          </FormItem>
          <FormItem prop="password" label="密码">
            <Input type="password" v-model="formReg.password"></Input>
          </FormItem>
          <FormItem prop="avatar" label="头像">
            <Upload ref="upload"
                    :format="['jpg','jpeg','png']"
                    :max-size="2048"
                    :show-upload-list="false"
                    :on-success="avatarUploadSuccess"
                    :on-format-error="avatarFormatError"
                    :on-exceeded-size="avatarMaxSize"
                    :on-error="avatarUploadError"
                    action="//120.79.85.160:80/pt/avatar/" v-model="formReg.avatar">
              <Button icon="ios-share-outline">上传头像 (可选)</Button>
            </Upload>
          </FormItem>
          <FormItem prop="city" label="所在城市">
            <Input type="text" v-model="formReg.city"></Input>
          </FormItem>
          <FormItem prop="address" label="详细地址">
            <Input type="text" v-model="formReg.address"></Input>
          </FormItem>
          <FormItem prop="intro" label="公司简介">
            <Input type="textarea" v-model="formReg.intro" :autosize="{minRows: 5,maxRows: 10}"
                   placeholder="可选，请输入一段简介"></Input>
          </FormItem>
          <FormItem>
            <Button type="default" @click="register">注册</Button>
          </FormItem>
        </Form>
        </Col>
      </Row>
    </div>


  </div>
</template>

<script>
  export default {
    name: 'Login',

    data() {
      return {
        buttonSize: 'large',
        regType: '兼职用户',
        showLoginTab: true,
        showRegTab: false,
        isPTUser: true,

        formLogin: {
          username: '',
          password: ''
        },
        ruleLogin: {
          username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'},
            {type: 'string', min: 5, message: '密码长度小于5位', trigger: 'blur'}
          ]
        },

        formReg: {
          username: '',
          password: '',
          avatar: '',
          city: '',
          phone_number: '',
          intro: '',

          companyUsername: '',
          address: '',
          params: '',
          regURI: '',
        },
        ruleReg: {
          username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'},
            {type: 'string', min: 5, message: '密码长度小于5位', trigger: 'blur'}
          ],
          city: [{required: true, message: '请输入所在地', trigger: 'blur'}],
          phone_number: [
            {required: true, message: '请输入手机号', trigger: 'blur'},
            {type: 'string', min: 11, max: 11, message: '请输入11位手机号', trigger: 'blur'}
          ],

          companyUsername: [{required: true, message: '请输入公司名', trigger: 'blur'}],
          address: [{required: true, message: '请输入详细地址', trigger: 'blur'}],
        },
      }
    },

    methods: {
      switchTab(e) {
        this.showLoginTab = !this.showLoginTab
        this.showRegTab = !this.showRegTab
      },
      switchRegType() {
        this.isPTUser = this.regType !== "兼职用户";
      }, // 跳转 this.$router.push({name: 'HelloWorld'});


      login() {
        var params = {
          username: this.formLogin.username,
          password: this.formLogin.password,
        }
        this.$axios.post(this.GLOBAL.host + '/pt/login/', this.$qs.stringify(this.formLogin), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'X-CSRFToken': this.$cookies.get('csrftoken')
          }
        }).then(res => {
          this.$Message.success('登录成功')
          this.$cookies.set("username_logged", this.formLogin.username)
          this.$router.replace({name: 'Index'})
          this.$router.go(0)
        }).catch(err => {
          this.$Message.error('登录失败，请先注销用户后登录或清空下 Cookies')
          this.logout()
        })
      },

      register() {
        if (this.isPTUser) {
          this.formReg.params = {
            username: this.formReg.username,
            password: this.formReg.password,
            Avatar: this.formReg.avatar,
            PhoneNumber: this.formReg.phone_number,
            City: this.formReg.city,
            Intro: this.formReg.intro
          }
          this.formReg.regURI = '/pt/ptu/'
        } else {
          this.formReg.params = {
            username: this.formReg.companyUsername,
            password: this.formReg.password,
            Avatar: this.formReg.avatar,
            Address: this.formReg.address,
            City: this.formReg.city,
            Intro: this.formReg.intro
          }
          this.formReg.regURI = '/pt/cu/'
        }

        this.$axios.post(this.GLOBAL.host + this.formReg.regURI, this.$qs.stringify(this.formReg.params), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          this.$Message.success('注册成功')
          this.$router.replace({name: 'Index'})
          this.$router.go(0)
        }).catch(err => {
          this.$Message.error('注册失败，请先注销用户后注册或清空下 Cookies')
          this.logout()
        })
      },

      avatarUploadSuccess(file) {
        this.formReg.avatar = file.fp
        this.$Message.success('头像上传成功，请继续注册')
      },


      avatarFormatError(file) {
        this.$Message.warning(file.name + '头像格式不正确，请选择 \'jpg\',\'jpeg\',\'png\'格式上传')
      },


      avatarMaxSize(file) {
        this.$Message.warning(file.name + '头像过大，上传限制为2M')
      },


      avatarUploadError() {
        this.$Message.error('头像上传失败，请重试')
      },

      logout() {
        this.$axios.get(this.GLOBAL.host + '/pt/logout/').then(res => {
          this.$cookies.remove('username_logged')
          this.$router.go(-1)
          this.$Message.success('已自动清空 Cookies，请重试')
        }).catch(err => {
          this.$Message.error('注销失败，请稍后重试')
        })
      }
    }
  }
</script>

<style>

</style>



