<template>
  <div id="JobDetails">
    <div class="container-job-details">


      <ul>
        <Row type="flex" justify="center">
          <Col>
          <li style="font-size: 32px; padding-top: 35px;">{{ jobDetails.JobName }}</li>
          </Col>
        </Row>

        <Row type="flex" justify="center" style="font-size: 18px;">
          <Col>
          <li>{{ jobDetails.JobCity }}·{{ jobDetails.JobLoc }}</li>
          </Col>
          <Divider type="vertical"/>
          <Col>
          <li>{{ jobDetails.JobWorkTime }}工作</li>
          </Col>
        </Row>

        <Row type="flex" justify="center">
          <Col span="">
          <li style="font-size: 24px;"><span style="color: coral">{{ jobDetails.JobSalary }}</span> 元</li>
          </Col>
        </Row>

        <Row type="flex" justify="center">
          <Col style="width: 550px;font-size: 16px;">
          <li><span style="font-size: 24px;">工作描述：</span><br><br>{{ jobDetails.JobDetail }}</li>
          </Col>
        </Row>
      </ul>
    </div>


    <div class="container-btns">
      <Row style="padding-top: 50px;">
        <Col offset="7">
        <Button type="default" icon="ios-at" @click="GotoCompany">所属公司</Button>
        </Col>
      </Row>

      <Row style="padding-top: 15px;">
        <Col offset="7">
        <Button type="default" icon="md-star-outline" @click="FavoriteJob">收藏岗位</Button>
        </Col>
      </Row>

      <Row style="padding-top: 15px;">
        <Col offset="7">
        <Button type="primary" icon="md-clipboard" @click="ApplyJob">应聘申请</Button>
        </Col>
      </Row>


    </div>
  </div>
</template>

<script>
  export default {
    name: 'JobDetails',

    data() {
      return {
        jobID: '',
        jobDetails: ''
      }
    },

    created() {
      this.getParam()
      this.requestJobDetails()
    },

    methods: {
      getParam() {
        this.jobID = this.$route.params.id
      },

      requestJobDetails() {
        this.$axios.get(this.GLOBAL.host + '/pt/jobs/' + this.jobID).then(res => {
          this.jobDetails = res.data
          this.$Message.success('获取兼职详情成功')
        }).catch(err => {
          this.$Message.error('获取兼职详情失败，请稍后重试')
        })
      },

      FavoriteJob() {
        var params = {
          BelongtoJob: this.jobDetails.id,
          JobName: this.jobDetails.JobName
        }

        this.$axios.post(this.GLOBAL.host + '/pt/favoritejobs/', this.$qs.stringify(params), {
          headers: {
            'X-CSRFToken': this.$cookies.get('csrftoken')
          }
        }).then(res => {
          this.$Message.success('收藏成功')
        }).catch(err => {
          this.$Message.error('收藏失败，不是兼职用户或未登录')
        })
      },

      ApplyJob() {
        var params = {
          BelongtoJob: this.jobDetails.id,
          BelongtoCompany: this.jobDetails.BelongtoCompany,
          JobName: this.jobDetails.JobName
        }

        this.$axios.post(this.GLOBAL.host + '/pt/candidates/', this.$qs.stringify(params), {
          headers: {
            'X-CSRFToken': this.$cookies.get('csrftoken')
          }
        }).then(res => {
          this.$Message.success('应聘提交成功')
        }).catch(err => {
          this.$Message.error('应聘提交失败，不是兼职用户或未登录')
        })
      },

      GotoCompany() {
        this.$router.push({name: 'CompanyDetails', params: {id: this.jobDetails.BelongtoCompany}})
      },
    }
  }
</script>

<style>
  .container-job-details ul {
    list-style: none;
    text-align: left;
  }

  .container-job-details li {
    padding: 10px 0;
  }
</style>



