<template>
  <div id="CompanyDetails">
    <div class="container-company-details">


      <Row type="flex" justify="center" style="font-size: 26px;padding-top: 60px;">
        <Col style="padding-right: 50px;">
        <Avatar :src="avatarSrc" shape="square" size="180"/>
        </Col>
        <Row style="line-height: 60px;">
          <Col>{{ companyDetails.Companyname }}</Col>
          <Col>{{ companyDetails.City }}</Col>
          <Col>{{ companyDetails.Address }}</Col>
        </Row>
      </Row>

      <Row type="flex" justify="center" style="padding:50px 0;">
        <Col style="width: 550px;font-size: 16px;">
          <span style="font-size: 24px;">公司简介：</span><br><br>{{ companyDetails.Intro }}
        </Col>
      </Row>


    </div>
  </div>
</template>

<script>
  export default {
    name: 'CompanyDetails',

    data() {
      return {
        companyID: '',
        avatarSrc: this.GLOBAL.host + '/pt/avatar/?id=',
        companyDetails: ''
      }
    },

    created() {
      this.getParam()
      this.requestCompanyDetails()
    },

    methods: {
      getParam() {
        this.companyID = this.$route.params.id
        this.avatarSrc = this.avatarSrc + this.$route.params.id
      },

      requestCompanyDetails() {
        this.$axios.get(this.GLOBAL.host + '/pt/cu/' + this.companyID).then(res => {
          this.companyDetails = res.data
          this.$Message.success('获取公司详情成功')
        }).catch(err => {
          this.$Message.error('获取公司详情失败，请稍后重试')
        })
      },
    }
  }
</script>

<style>
  .container-job-details ul {
    list-style: none;
    text-align: left;
  }

  .container-job-details li {
    padding: 10px 0;
  }
</style>



