<template>
  <div id="PersonalCenter">
    <div class="container-personal-center">


      <Row type="flex" justify="center" style="">
        <Col>
        <div class="title">
          修改资料
        </div>
        </Col>
      </Row>

      <Row type="flex" justify="center" style="">
        <Col>
        <Form label-position="left" :label-width="80" v-if="isPartTimeUser">
          <FormItem prop="avatar" label="头像">
            <Upload ref="upload"
                    :format="['jpg','jpeg','png']"
                    :max-size="2048"
                    :show-upload-list="false"
                    :on-success="avatarUploadSuccess"
                    :on-format-error="avatarFormatError"
                    :on-exceeded-size="avatarMaxSize"
                    :on-error="avatarUploadError"
                    action="//127.0.0.1:9000/pt/avatar/" v-model="formUpdate.avatar">
              <Button icon="ios-share-outline">更新头像</Button>
            </Upload>
          </FormItem>
          <FormItem prop="city" label="所在城市">
            <Input type="text" v-model="formUpdate.city"></Input>
          </FormItem>
          <FormItem prop="phone_number" label="手机号">
            <Input type="text" v-model="formUpdate.phone_number"></Input>
          </FormItem>
          <FormItem label="个人简介" prop="intro">
            <Input type="textarea" v-model="formUpdate.intro" :autosize="{minRows: 5,maxRows: 10}"></Input>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="updateProfile">更新资料</Button>
          </FormItem>
        </Form>

        <Form label-position="left" :label-width="80" v-if="!isPartTimeUser">
          <FormItem prop="avatar" label="头像">
            <Upload ref="upload"
                    :format="['jpg','jpeg','png']"
                    :max-size="2048"
                    :show-upload-list="false"
                    :on-success="avatarUploadSuccess"
                    :on-format-error="avatarFormatError"
                    :on-exceeded-size="avatarMaxSize"
                    :on-error="avatarUploadError"
                    action="//127.0.0.1:9000/pt/avatar/" v-model="formUpdate.avatar">
              <Button icon="ios-share-outline">更新头像</Button>
            </Upload>
          </FormItem>
          <FormItem prop="city" label="所在城市">
            <Input type="text" v-model="formUpdate.city"></Input>
          </FormItem>
          <FormItem prop="address" label="地址">
            <Input type="text" v-model="formUpdate.address"></Input>
          </FormItem>
          <FormItem label="公司简介" prop="intro">
            <Input type="textarea" v-model="formUpdate.intro" :autosize="{minRows: 5,maxRows: 10}"></Input>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="updateProfile">更新资料</Button>
          </FormItem>
        </Form>
        </Col>
      </Row>

      <Row type="flex" justify="center" style="">
        <Col>
        <div class="title">
          {{ candidateText }}
        </div>
        </Col>
      </Row>

      <div style="width: 500px; margin:0 auto;" v-if="isPartTimeUser">
        <Table :columns="partTimeUserColumns" :data="jobsApplyList"></Table>
      </div>
      <div style="width: 500px; margin:0 auto;" v-if="!isPartTimeUser">
        <Table :columns="CompanyColumns" :data="candidatesList"></Table>
      </div>

      <Row type="flex" justify="center" style="">
        <Col>
        <div class="title">
          {{ jobText }}
        </div>
        </Col>
      </Row>

      <div style="width: 500px; margin:0 auto;" v-if="isPartTimeUser">
        <Table :columns="favjobsColumns" :data="favJobsList"></Table>
      </div>

      <Row type="flex" justify="center" v-if="!isPartTimeUser">
        <Col>
        <div>
          <Form label-position="left" :label-width="80">
            <FormItem prop="jobName" label="岗位名称">
              <Input type="text" v-model="formJob.jobName"></Input>
            </FormItem>
            <FormItem prop="city" label="所在城市">
              <Input type="text" v-model="formJob.jobCity"></Input>
            </FormItem>
            <FormItem prop="jobLoc" label="详细地址">
              <Input type="text" v-model="formJob.jobLoc"></Input>
            </FormItem>
            <FormItem prop="jobSalary" label="工资薪酬">
              <Input type="text" v-model="formJob.jobSalary"></Input>
            </FormItem>
            <FormItem prop="jobWorkTime" label="工作时间">
              <Input type="text" v-model="formJob.jobWorkTime"></Input>
            </FormItem>
            <FormItem label="工作详情" prop="jobDetail">
            <Input type="textarea" v-model="formJob.jobDetail" :autosize="{minRows: 5,maxRows: 10}"></Input>
          </FormItem>
            <FormItem>
              <Button type="primary" @click="addJob">添加工作</Button>
            </FormItem>
          </Form>
        </div>
        </Col>
      </Row>

    </div>
  </div>
</template>

<script>
  export default {
    name: 'PersonalCenter',

    data() {
      return {
        isPartTimeUser: false,
        uid: '',
        candidateText: '',
        jobText: '',
        formUpdate: {
          avatar: '',
          phone_number: '',
          city: '',
          address: '',
          intro: '',
        },

        formJob: {
          jobName: '',
          jobLoc: '',
          jobSalary: '',
          jobWorkTime: '',
          jobCity: '',
          jobDetail: ''
        },

        partTimeUserColumns: [
          {title: '岗位', key: 'JobName'},
          {title: '是否录用', key: 'isHire'},
          {
            title: '操作',
            key: 'action',
            fixed: 'right',
            width: 100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                  },
                  on: {
                    click: () => {
                      this.GotoJobDetails(params.index, true)
                    }
                  }
                }, '浏览')]);
            },
          }
        ],
        jobsApplyList: [],
        CompanyColumns: [
          {title: '岗位', key: 'JobName'},
          {title: '应聘者', key: 'CandidateName'},
          {title: '是否录用', key: 'isHire'},
          {
            title: '操作',
            key: 'action',
            fixed: 'right',
            width: 100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                  },
                  on: {
                    click: () => {
                      this.candidatePass(params.index)
                    }
                  }
                }, '通过')]);
            },
          },
        ],
        candidatesList: [],

        favjobsColumns: [
          {title: '岗位', key: 'JobName'},
          {
            title: '操作',
            key: 'action',
            fixed: 'right',
            width: 100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                  },
                  on: {
                    click: () => {
                      this.GotoJobDetails(params.index, false)
                    }
                  }
                }, '浏览')]);
            },
          }],
        favJobsList: []
      }
    },

    created() {
      setTimeout("",5000)
      this.checkUserType()
    },

    methods: {
      checkUserType() {
        this.$axios.get(this.GLOBAL.host + '/pt/checkusertype/', {
          headers: {'X-CSRFToken': this.$cookies.get('csrftoken')}
        }).then(res => {
          this.isPartTimeUser = res.data.user_type === 'pt'
          this.uid = res.data.user_id
          if (this.isPartTimeUser) {
            this.getJobsApplyList()
            this.getFavoriteJobs()
            this.candidateText = '已提交申请岗位'
            this.jobText = '已收藏岗位'
          } else {
            this.getCandidatesList()
            this.candidateText = '待处理应聘申请'
            this.jobText = '添加工作'
          }
        }).catch(err => {
          console.log(err)
          this.$Message.error('请稍后重试')
        })
      },

      avatarUploadSuccess(file) {
        this.formUpdate.avatar = file.fp
        this.$Message.success('头像上传成功')
      },
      avatarFormatError(file) {
        this.$Message.warning(file.name + '格式不正确，请选择 \'jpg\',\'jpeg\',\'png\'格式上传')
      },
      avatarMaxSize(file) {
        this.$Message.warning(file.name + '过大，上传限制为2M')
      },
      avatarUploadError() {
        this.$Message.error('上传失败，请稍后重试')
      },

      updateProfile() {
        if (this.formUpdate.avatar) {
          var Avatar = this.formUpdate.avatar
        }
        if (this.formUpdate.city) {
          var City = this.formUpdate.city
        }
        if (this.formUpdate.intro) {
          var Intro = this.formUpdate.intro
        }

        if (this.isPartTimeUser) {
          if (this.formUpdate.phone_number) {
            var PhoneNumber = this.formUpdate.phone_number
          }
          var params = {
            Avatar,
            City,
            PhoneNumber,
            Intro,
          }
        } else {
          if (this.formUpdate.address) {
            var Address = this.formUpdate.address
          }
          var params = {
            Avatar,
            City,
            Address,
            Intro,
          }
        }

        this.$axios.patch(this.GLOBAL.host + '/pt/profile/', this.$qs.stringify(params), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded', 'X-CSRFToken': this.$cookies.get('csrftoken')}
        }).then(res => {
          this.$Message.success('更新成功')
        }).catch(err => {
          this.$Message.error('更新失败，请稍后重试')
        })
      },

      // =========================== 兼职用户 ===========================

      getJobsApplyList() {
        this.$axios.get(this.GLOBAL.host + '/pt/candidates/', {
          headers: {'X-CSRFToken': this.$cookies.get('csrftoken')}
        }).then(res => {
          this.jobsApplyList = res.data
        }).catch(err => {
          console.log(err)
          this.$Message.error('获取已申请岗失败，请稍后重试')
        })
      },

      getFavoriteJobs() {
        this.$axios.get(this.GLOBAL.host + '/pt/favoritejobs/', {
          headers: {'X-CSRFToken': this.$cookies.get('csrftoken')}
        }).then(res => {
          this.favJobsList = res.data
        }).catch(err => {
          console.log(err)
          this.$Message.error('获取收藏岗位失败，请稍后重试')
        })
      },

      GotoJobDetails(index, isApply) {
        if (isApply) {
          var jobID = this.jobsApplyList[index].BelongtoJob
        } else {
          var jobID = this.favJobsList[index].BelongtoJob
        }
        this.$router.push({name: 'JobDetails', params: {id: jobID}})
      },

      // =========================== 公司用户 ===========================

      getCandidatesList() {
        this.$axios.get(this.GLOBAL.host + '/pt/candidates/', {
          headers: {'X-CSRFToken': this.$cookies.get('csrftoken')}
        }).then(res => {
          console.log(res)
          this.candidatesList = res.data
        }).catch(err => {
          console.log(err)
          this.$Message.error('获取已申请岗失败，请稍后重试')
        })
      },

      candidatePass(index) {
        var params = {
          isHire: true
        }

        this.$axios.patch(this.GLOBAL.host + '/pt/candidates/' + this.candidatesList[index].id, this.$qs.stringify(params), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded', 'X-CSRFToken': this.$cookies.get('csrftoken')}
        }).then(res => {
          this.$Message.success('更新成功')
        }).catch(err => {
          this.$Message.error('更新失败，请稍后重试')
        })
      },

      addJob() {
        var params = {
          "JobName": this.formJob.jobName,
          "JobLoc": this.formJob.jobLoc,
          "JobSalary": this.formJob.jobSalary,
          "JobWorkTime": this.formJob.jobWorkTime,
          "JobCity": this.formJob.jobCity,
          "JobDetail": this.formJob.jobDetail,
          "BelongtoCompany": this.uid,
        }
        console.log(params)

        this.$axios.post(this.GLOBAL.host + '/pt/jobs/create/', this.$qs.stringify(params), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded', 'X-CSRFToken': this.$cookies.get('csrftoken')}
        }).then(res => {
          this.$Message.success('添加工作成功')
        }).catch(err => {
          this.$Message.error('添加工作失败，请稍后重试')
        })
      },
    }
  }
</script>

<style>
  .title {
    padding: 50px 10px 10px 10px;
    margin-bottom: 35px;
    border-bottom: #abd8d7 3px solid;
    font-size: 25px;
    color: #3984b9;
  }
</style>














